.Search {
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 1000;
    width: 100%;
    background-color: rgb(255, 133, 42);
}
.Search .header-bd {
    width: 100%;
    padding-right: 9px;
    padding-left: 42px;
    padding-top: 6px;
    padding-bottom: 6px;
    background-size: 27px 20px;
    background-image: url(../assets/taobao.PNG);
    background-repeat: no-repeat;
    background-position: 9px center;
}
.Search .header-bd .placeholder {
    display: block;
    font-size: 0px;
    background-color: rgb(255, 78, 34);
    text-align: center;
    border-radius: 4px;
}
a {
    text-decoration: none;
}
.Search .header-bd .placeholder .text {
    height: 25px;
    line-height: 25px;
    font-size: 13px;
    display: inline-block;
    color: rgb(240, 224, 220);
}
.Search .header-bd .placeholder .text::before {
    font-size: 15px;
    display: inline-block;
    color: rgb(255, 255, 255);
    margin-right: 5px;
}
.recommend-container {
    top:37px;
    display: -webkit-flex;
    display: flex;
    position: relative;
    box-sizing: border-box;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
    flex-direction: row;
    place-content: flex-start space-between;
    flex-shrink: 0;
    overflow: hidden;
    -webkit-box-pack: justify;
    -webkit-box-lines: multiple;
    -webkit-flex-wrap: wrap;
    flex-wrap: wrap;
    padding: 0 7.5px;
    background: #f2f2f2;
    padding-bottom: 12px;
}
.recommend-img-wrapper {
    -webkit-box-align: center;
    height: 260px;
    width: 170.99999999999997px;
    align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
}
/* .recommend-item img{
    width: 10px;
} */
.recommend-item {
    height: 200px;
    position: relative;
    width: 170.99999999999997px;
    box-sizing: border-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    flex-direction: column;
    align-content: flex-start;
    flex-shrink: 0;
    background: #fff;
    margin-top: 9px;
    border-radius: 6px;
    overflow: hidden;
    margin-left: 4.5px;
    margin-right: 4.5px;
}
