.action-bar-wrap {
    height: 50px;
    width: 100%;
    background-color: #FFF;
    line-height: 50px;
    display: -webkit-box;
    display: flex;
    text-align: center;
    padding-bottom: var(--safe-area-inset-bottom);
    bottom: 0px;
    position: fixed;
}
.action-bar-wrap button{
    border-style: none;
}

.cart {
    /* width: 300px; */
    flex: 1 1;
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 0%;
    color: #fff;
    background-color: #FF9500;
   
    line-height: 34px;
    font-size: 15px;
    line-height: 34px;
}
.buy {
    /* width: 300px; */
    flex: 1 1;
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 0%;
    color: #fff;
    background-color: red;
    
    line-height: 34px;
    font-size: 15px;
    line-height: 34px;
}